import { Injectable } from "@angular/core";
import { HttpClient, HttpParams } from "@angular/common/http";
import { Observable } from "rxjs/internal/Observable";
import { PagedResponse } from "src/app/shared/models/shared/paged.response";
import { GetDataUnitsRequest } from "./data-unit.input";
import { DataUnit } from "src/app/shared/models/laboratory-management/data-unit.model";

/**
 * DataUnit Service
 */
@Injectable()
export class DataUnitService {
  path = `v1/common-types/data-units`;
  constructor(private readonly http: HttpClient) {}

  /**
   * Get DataUnits
   */
  getDataUnites(
    input: GetDataUnitsRequest
  ): Observable<PagedResponse<DataUnit>> {
    return this.http.get<PagedResponse<DataUnit>>(`${this.path}`, {
      params: new HttpParams({ fromObject: input as any }),
    });
  }
}
