import { Component } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { take } from "rxjs/operators";
import { indicate } from "src/app/shared/operators/operators";
import { TableComponent } from "src/app/shared/components/table.component";
import { LaboratoryValueReferenceService } from "src/app/shared/services/laboratory-management/laboratory-value-reference/laboratory-value-reference.service";
import { GetValueReferencesRequest } from "src/app/shared/services/laboratory-management/laboratory-value-reference/laboratory-value-reference.input";
import { LaboratoryValueReference } from "src/app/shared/models/laboratory-management/laboratory-value-reference.model";
import { CreateComponent } from "./components/create/create.component";
import { LaboratoryValueReferencesFilter } from "./components/filter/value-references.filter";
import { FilterComponent } from "./components/filter/filter.component";
import { ValueReferenceDeleteComponent } from "./components/value-reference/delete/value-reference-delete.component";

@Component({
  selector: "app-laboratory-management-value-references",
  templateUrl: "./value-references.component.html",
})
export class LaboratoryValueReferencesComponent extends TableComponent<LaboratoryValueReference> {
  displayedColumns = [
    "TestType",
    "AgeGroup",
    "Sex",
    "DataUnit",
    "Data",
    "menu",
  ];
  override filter: LaboratoryValueReferencesFilter = {};

  /**
   * Constructor
   * @param dialog
   * @param laboratoryValueReferenceService
   */
  constructor(
    private dialog: MatDialog,
    private laboratoryValueReferenceService: LaboratoryValueReferenceService
  ) {
    super();
    this.get();
  }

  /**
   * Open create component
   */
  openCreateComponent(): void {
    const ref = this.dialog.open(CreateComponent, {
      autoFocus: true,
      width: "480px",
    });
    ref
      .afterClosed()
      .pipe(take(1))
      .subscribe((res) => {
        if (res) {
          this.add(res);
        }
      });
  }

  /**
   * Open filter component
   */
  openFilterComponent(): void {
    const ref = this.dialog.open(FilterComponent, {
      autoFocus: true,
      width: "480px",
      data: this.filter,
    });
    ref
      .afterClosed()
      .pipe(take(1))
      .subscribe((filter) => {
        if (filter) {
          this.filter = filter;
          this.get();
        }
      });
  }

  /**
   * Open ValueReference delete component
   */
  openValueReferenceDeleteComponent(item: LaboratoryValueReference): void {
    const ref = this.dialog.open(ValueReferenceDeleteComponent, {
      autoFocus: true,
      width: "480px",
      data: item.id,
    });
    ref
      .afterClosed()
      .pipe(take(1))
      .subscribe((res) => {
        if (res) {
          this.delete(res);
        }
      });
  }

  /**
   * Display values
   * @param item
   * @returns
   */
  displayValues(item: LaboratoryValueReference) {
    if (item.value) {
      return item.value;
    }
    return `${item.start} - ${item.end}`;
  }

  /**
   * Get
   */
  get(): void {
    const input: GetValueReferencesRequest = {
      pageIndex: this.pageIndex,
      pageSize: this.pageSize,
      sortBy: this.sortBy,
      sortDescending: this.sortDescending,
      description: this.filterValue,
    };
    if (this.filter.comparisonOperator) {
      input.comparisonOperator = this.filter.comparisonOperator;
    }
    if (this.filter.ageGroupId) {
      input.ageGroupId = this.filter.ageGroupId;
    }
    if (this.filter.dataUnitId) {
      input.dataUnitId = this.filter.dataUnitId;
    }
    if (this.filter.sex) {
      input.sex = this.filter.sex;
    }
    if (this.filter.testTypeId) {
      input.testTypeId = this.filter.testTypeId;
    }

    this.laboratoryValueReferenceService
      .getValueReferences(input)
      .pipe(indicate(this.indicator$))
      .pipe(take(1))
      .subscribe((res) => {
        this.set(res.count, res.items);
      });
  }
}
