import { Component } from "@angular/core";
import { MatDialogRef } from "@angular/material/dialog";
import { FormBuilder, Validators } from "@angular/forms";
import { DialogComponent } from "src/app/shared/components/dialog.component";
import { handleError, indicate } from "src/app/shared/operators/operators";
import { map, take } from "rxjs/operators";
import { Observable } from "rxjs/internal/Observable";
import { TestTypeService } from "src/app/shared/services/analysis-management/test-type/test-type.service";
import { TestType } from "src/app/shared/models/analysis-management/test-type.model";
import { LaboratoryValueReferenceService } from "src/app/shared/services/laboratory-management/laboratory-value-reference/laboratory-value-reference.service";
import { LaboratoryValueReference } from "src/app/shared/models/laboratory-management/laboratory-value-reference.model";
import { AgeGroup } from "src/app/shared/models/laboratory-management/age-group.model";
import { AgeGroupService } from "src/app/shared/services/laboratory-management/age-group/age-group.service";
import { DataUnitService } from "src/app/shared/services/common-types/data-unit/data-unit.service";
import { DataUnit } from "src/app/shared/models/laboratory-management/data-unit.model";
import { ComparisonOperator } from "src/app/shared/models/types/comparison-operator.model";
import { Sex } from "src/app/shared/models/types/sex.model";

@Component({
  selector: "app-laboratory-management-value-references-create",
  templateUrl: "./create.component.html",
  providers: [TestTypeService, AgeGroupService, DataUnitService],
})
export class CreateComponent extends DialogComponent<
  CreateComponent,
  LaboratoryValueReference
> {
  testTypes$: Observable<TestType[] | null> | null = null;
  sexes: string[] = [];
  comparisonOperators: ComparisonOperator[] = [];
  ageGroups$: Observable<AgeGroup[] | null> | null = null;
  dataUnits$: Observable<DataUnit[] | null> | null = null;

  /**
   * Constructor
   * @param laboratoryValueReferenceService
   * @param testTypeService
   * @param ageGroupService
   * @param dataUnitService
   * @param fb
   * @param dialog
   */
  constructor(
    private laboratoryValueReferenceService: LaboratoryValueReferenceService,
    private testTypeService: TestTypeService,
    private ageGroupService: AgeGroupService,
    private dataUnitService: DataUnitService,
    private fb: FormBuilder,
    private dialog: MatDialogRef<CreateComponent>
  ) {
    super(
      dialog,
      fb.group({
        description: [null, []],
        testTypeId: [null, [Validators.required]],
        comparisonOperator: [null, [Validators.required]],
        sex: [null, []],
        ageGroupId: [null, [Validators.required]],
        dataUnit: [null, []],
        value: [null, []],
        start: [null, []],
        end: [null, []],
      })
    );
    this.getTestTypes();
    this.getAgeGroups();
    this.getDataUnits();
    this.sexes = Object.values(Sex);
    this.comparisonOperators = Object.values(ComparisonOperator);
  }

  /**
   * Get test types
   */
  private getTestTypes(): void {
    this.testTypes$ = this.testTypeService
      .getTestTypes({
        pageIndex: 1,
        pageSize: 50,
        sortBy: "Name",
        sortDescending: false,
      })
      .pipe(map((x) => x.items));
  }

  /**
   * Get data units
   */
  private getDataUnits(): void {
    this.dataUnits$ = this.dataUnitService
      .getDataUnites({
        pageIndex: 1,
        pageSize: 50,
        sortBy: "Name",
        sortDescending: false,
      })
      .pipe(map((x) => x.items));
  }

  /**
   * Get age groups
   */
  private getAgeGroups(): void {
    this.ageGroups$ = this.ageGroupService
      .getAgeGroups({
        pageIndex: 1,
        pageSize: 50,
        sortBy: "Name",
        sortDescending: false,
      })
      .pipe(map((x) => x.items));
  }

  /**
   * Is range
   * @returns
   */
  isRange(): boolean {
    return (
      this.form.value.valueOperation?.code === "<>" ||
      this.form.value.valueOperation?.code == "<==>"
    );
  }

  /**
   * Submit
   */
  override handleSubmit(): void {
    const value = this.form.value;
    this.laboratoryValueReferenceService
      .createValueReference({
        description: value.description,
        testTypeId: value.testTypeId,
        comparisonOperator: value.comparisonOperator,
        ageGroupId: value.ageGroupId,
        dataUnitId: value.dataUnit?.id,
        sex: value.sex,
        start: value.start,
        end: value.end,
        value: value.value,
      })
      .pipe(indicate(this.indicator$))
      .pipe(handleError(this.alert$))
      .pipe(take(1))
      .subscribe((res) => {
        this.close(res);
      });
  }
}
