import { Injectable } from "@angular/core";
import { HttpClient, HttpParams } from "@angular/common/http";
import { Observable } from "rxjs/internal/Observable";
import { PagedResponse } from "src/app/shared/models/shared/paged.response";
import {
  CreateValueReferenceRequest,
  GetValueReferencesRequest,
} from "./laboratory-value-reference.input";
import { LaboratoryValueReference } from "src/app/shared/models/laboratory-management/laboratory-value-reference.model";

/**
 * LaboratoryValueReference Service
 */
@Injectable()
export class LaboratoryValueReferenceService {
  path = `laboratory-management/v1/value-references`;
  constructor(private readonly http: HttpClient) {}

  /**
   * Get ValueReferences
   */
  getValueReferences(
    input: GetValueReferencesRequest
  ): Observable<PagedResponse<LaboratoryValueReference>> {
    return this.http.get<PagedResponse<LaboratoryValueReference>>(
      `${this.path}`,
      {
        params: new HttpParams({ fromObject: input as any }),
      }
    );
  }

  /**
   * Create ValueReference
   */
  createValueReference(
    input: CreateValueReferenceRequest
  ): Observable<LaboratoryValueReference> {
    return this.http.post<LaboratoryValueReference>(`${this.path}`, input);
  }

  /**
   * Delete ValueReference
   */
  deleteValueReference(laboratoryValueReferenceId: string): Observable<object> {
    return this.http.delete(`${this.path}/${laboratoryValueReferenceId}`);
  }
}
