import { Injectable } from "@angular/core";
import { HttpClient, HttpParams } from "@angular/common/http";
import { Observable } from "rxjs/internal/Observable";
import { PagedResponse } from "src/app/shared/models/shared/paged.response";
import { GetAgeGroupsRequest } from "./age-group.input";
import { AgeGroup } from "src/app/shared/models/laboratory-management/age-group.model";

/**
 * AgeGroup Service
 */
@Injectable()
export class AgeGroupService {
  path = `laboratory-management/v1/age-groups`;
  constructor(private readonly http: HttpClient) {}

  /**
   * Get Test types
   */
  getAgeGroups(
    input: GetAgeGroupsRequest
  ): Observable<PagedResponse<AgeGroup>> {
    return this.http.get<PagedResponse<AgeGroup>>(`${this.path}`, {
      params: new HttpParams({ fromObject: input as any }),
    });
  }
}
