import { Component, Inject } from "@angular/core";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material/dialog";
import { DialogComponent } from "src/app/shared/components/dialog.component";
import { FormGroup } from "@angular/forms";
import { indicate, handleError } from "src/app/shared/operators/operators";
import { take } from "rxjs/operators";
import { LaboratoryValueReferenceService } from "src/app/shared/services/laboratory-management/laboratory-value-reference/laboratory-value-reference.service";

@Component({
  selector: "app-laboratory-management-value-references-value-reference-delete",
  templateUrl: "./value-reference-delete.component.html",
})
export class ValueReferenceDeleteComponent extends DialogComponent<
  ValueReferenceDeleteComponent,
  string
> {
  /**
   * Constructor
   * @param laboratoryValueReferenceService
   * @param dialog
   * @param data
   */
  constructor(
    private laboratoryValueReferenceService: LaboratoryValueReferenceService,
    private dialog: MatDialogRef<ValueReferenceDeleteComponent>,
    @Inject(MAT_DIALOG_DATA) public data: string
  ) {
    super(dialog, new FormGroup({}));
  }

  /**
   * Submit
   */
  override handleSubmit(): void {
    this.laboratoryValueReferenceService
      .deleteValueReference(this.data)
      .pipe(indicate(this.indicator$))
      .pipe(handleError(this.alert$))
      .pipe(take(1))
      .subscribe(() => {
        this.close(this.data);
      });
  }
}
